      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="160">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope.row)" type="text" size="small" v-if="authority.b_info==1">
                胎号查看
              </el-button>
              <el-button @click="untilDetail(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status == 1 && authority.b_add==1">
                胎号录入
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                胎号录入
              </el-button>

            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="130" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="instorage_sn" width="220" label="入库单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" width="140" label="品牌名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" width="250" label="商品编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="商品名称">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_num" width="130" label="返胎数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_num" width="100" label="胎号数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="160" label="货位名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="120" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="170" label="单据时间">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="260" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-sizes="per_page" :page-size="pageSize1" @current-change="handleCurrentChange" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <div v-if="dialogFormVisible">
        <el-dialog :close-on-click-modal="false" title="胎号录入" :visible.sync="dialogFormVisible" width="700px">
          <el-form :inline="true" :model="add" ref="ruleForm" class="dialog-see">
            <el-form-item label="销售组织：" class="hidden">
              <span>{{add.company_name}}</span>
            </el-form-item>
            <el-form-item label="返胎入库单据号：">
              {{add.instorage_sn}}
            </el-form-item>
            <el-form-item label="客户名称：" class="hidden ys">
              <span>{{add.customer_name}}</span>
            </el-form-item>
            <el-form-item label="品牌名称：" class="hidden">
              <span>{{add.brand_name}}</span>
            </el-form-item>
            <el-form-item label="商品编号：" class="hidden">
              <span>{{add.goods_id}}</span>
            </el-form-item>
            <el-form-item label="商品名称：" class="hidden">
              <span>{{add.goods_name}}</span>
            </el-form-item>

            <el-form-item label="部 门：" class="hidden">
              <span>{{add.department_name}}</span>
            </el-form-item>

          </el-form>

          <span style="width:100%;height:1px;background:#666;display:block;margin-bottom:20px;"></span>

          <el-form :inline="true">
            <el-row>
              <h3 style="margin-bottom:20px">胎号录入</h3>
            </el-row>
            <el-row>
              <el-form-item v-for="item in add.tire_json" :key="item.id" label="胎号" style="width:290px;">
                <el-input type="text" v-model="item.tire_un" placeholder="请输入" style="width:160px"></el-input>
              </el-form-item>
            </el-row>

          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="clearAdd">取 消</el-button>
            <el-button type="primary" @click="Sure">确 定</el-button>
          </div>
        </el-dialog>
      </div>

      <div v-if="dialogFormVisibleSee">
        <el-dialog :close-on-click-modal="false" title="胎号查看" :visible.sync="dialogFormVisibleSee" width="700px">

          <el-form :inline="true" :model="GoodsTire" ref="ruleForm" class="dialog-see">
            <el-form-item label="销售组织：">
              {{GoodsTire.company_name}}
            </el-form-item>
            <el-form-item label="返胎入库单据号：">
              {{GoodsTire.instorage_sn}}
            </el-form-item>

            <el-form-item label="客户名称：">
              {{GoodsTire.customer_name}}
            </el-form-item>

            <el-form-item label="品牌名称：">
              {{GoodsTire.brand_name}}
            </el-form-item>

            <el-form-item label="商品编号：">
              {{GoodsTire.goods_id}}
            </el-form-item>

            <el-form-item label="商品名称：">
              {{GoodsTire.goods_name}}
            </el-form-item>

            <el-form-item label="部 门：">
              {{GoodsTire.department_name}}
            </el-form-item>

          </el-form>
          <span style="width:100%;height:1px;background:#666;display:block;margin-bottom:20px;"></span>
          <el-table :highlight-current-row="true" tooltip-effect="light" border :data="GoodsTire.tire_list" style="width: 100%" max-height="600">
            <el-table-column type="index" label="序号" width="60">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" min-width="100">
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="check_status" label="校验状态">
            </el-table-column>
          </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisibleSee=false">取 消</el-button>
            <el-button type="primary" @click="dialogFormVisibleSee=false">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>

  </div>
</template>
<script>
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "aftersaleThreetireIndex",
  data() {
    return {
      authority: {},
      add: {
        tire_json: []
      },
      allocationInventoryList: [], //货位列表
      goodsList: [], //商品下拉
      GoodsTire: {}, //查看胎号

      TableData: {
        tableData: [],
        total: 10
      },
      per_page: [30, 50, 100, 500],
      searchParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: false,
      timeValue: [],
      companyList: {
        options: []
      },
      departmentList: {
        options: []
      },
      params: {
        tire_type: "1"
      },
      multipleSelection: [], //已选项
      dialogFormVisible: false,
      dialogFormVisibleSee: false,
      showDepartment: true,
      departmentList: {
        options: []
      },
      companyList: [],
      SelesTomer: {
        options: []
      }, //客户列表
      warehouseList: {
        options: []
      } //仓库管理
    };
  },
  methods: {
    ...mapActions(["tiremanagementSeeACT"]),
    getCompanyFoucs() {
      getCompanyListCommon(this); //获取组织
    },
    getDepartmentFocus() {
      getDepartmentListCommon(this, this.params);
    },
    getWarehouseFoucs() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id
      }); //仓库下拉
    },
    getAllocationInventoryFoucs() {
      this.$axMethod.allocationInventoryList().then(({ data }) => {
        if (data.code == "200") {
          this.allocationInventoryList = data.data;
        } //货位列表下拉
      });
    },
    clearAdd() {
      // 弹框取消
      this.$set(this, "add", {
        tire_json: []
      });
      this.dialogFormVisible = false;
    },
    changeAll(val) {
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: val
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.allocationInventoryList = data.data;
          } //货位列表下拉
        });
    },
    Sure() {
      // 弹框确认取消
      for (var i = 0; i < this.add.tire_json.length; i++) {
        if (!this.add.tire_json[i].tire_un) {
          this.$message({
            message: "胎号需全部录入",
            type: "warning"
          });
          return false;
        }
      }
      let json_infos = {
        instorage_goods_id: this.add.instorage_goods_id,
        goods_id: this.add.goods_id,
        goods_sn: this.add.goods_sn,
        tire_json: JSON.stringify(this.add.tire_json)
      };

      this.$axMethod.addGoodsTire(json_infos).then(response => {
        if (response.data.code == 200) {
          this.$message({
            message: response.data.data,
            type: "success"
          });
          getData(this.searchParams);
        }
      });
      // this.$set(this, "add", { tire_json: [] });
      this.dialogFormVisible = false;
    },
    companyChange(val, formName) {
      // 获取部门
      getDepartmentListCommon(this, this[formName], 1);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    untilDetail(scope, row) {
      // 胎号录入
      this.$set(this, "add", {
        instorage_goods_id: row.instorage_goods_id,
        goods_id: row.goods_id,
        instorage_sn: row.instorage_sn,
        customer_name: row.customer_name,
        brand_name: row.brand_name,
        goods_sn: row.goods_sn,
        goods_name: row.goods_name,
        goods_num: row.goods_num,
        // tire_num: row.tire_num,
        company_id: row.company_id,
        department_id: row.department_id,
        company_name: row.company_name,
        department_name: row.department_name,
        tire_json: []
      });

      for (var i = 0; i < row.goods_num; i++) {
        this.add.tire_json.push({});
      }
      this.dialogFormVisible = true;
    },
    seeDetail(row) {
      this.$axMethod
        .getGoodsTire({
          instorage_goods_id: row.instorage_goods_id
        })
        .then(res => {
          this.GoodsTire = res.data.data;
        });
      this.dialogFormVisibleSee = true;
    },
    onReset(formName) {
      this.$set(this, "params", {
        tire_type: "1"
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          tire_type: "1"
        },
        TableData: this.TableData, //列表数据
        API: "getGoodsTireList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.add_time_start = this.timeValue[0];
        this.params.add_time_end = this.timeValue[1];
      } else {
        this.params.add_time_start = this.params.add_time_end = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "searchParams", {
      params: {
        tire_type: "1"
      }, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getGoodsTireList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

<style scoped>
.el-dialog {
  width: 650px;
}

.hidden {
  width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dialog-see .el-form-item--mini .el-form-item__content,
.dialog-see .el-form-item--mini .el-form-item__label {
